import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Subscription } from 'rxjs';
import { debounceTime, filter } from 'rxjs/operators';
import { Asset } from '../services/asset/asset.types';
import { AssetService } from '../services/asset/asset.service';

@Component({
  selector: 'app-assets',
  templateUrl: './assets.component.html',
  styleUrls: ['./assets.component.scss']
})
export class AssetsComponent implements OnInit, OnDestroy {
  public searchForm: FormGroup;
  public assets: Asset[] = [];
  public isSearching = false;
  private offset: number;
  private takeSize = 20;
  public isGettingNextAssets = false;
  public shouldGetNextAssets = false;
  public searchSubcription: Subscription;

  constructor(formBuilder: FormBuilder, private assetService: AssetService) {
    this.searchForm = formBuilder.group({
      text: ''
    });
  }

  public ngOnInit() {
    this.initiateFreshSearchSettings();
    this.searchForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length >= 3 || textValue.length === 0)
      )
      .subscribe(() => this.submitSearch());
    this.loadNextAssetlist();
  }
  public ngOnDestroy() {
    this.safelyDestroySearchSubscription();
  }

  private submitSearch() {
    this.initiateFreshSearchSettings();
    this.loadNextAssetlist();
  }

  private initiateFreshSearchSettings() {
    this.assets = [];
    this.offset = 0;
    this.shouldGetNextAssets = true;
  }

  public loadNextAssetlist() {
    this.isGettingNextAssets = true;
    this.safelyDestroySearchSubscription();
    this.searchSubcription = this.assetService
      .listAssets({
        offset: this.offset,
        take: this.takeSize,
        text: this.searchForm.value.text
      })
      .subscribe((assets: Asset[]) => {
        this.assets.push(...assets);
        this.offset += this.takeSize;
        if (assets.length < this.takeSize) {
          this.shouldGetNextAssets = false;
        }
        this.isGettingNextAssets = false;
      });
  }

  public safelyDestroySearchSubscription() {
    this.searchSubcription &&
      !this.searchSubcription.closed &&
      this.searchSubcription.unsubscribe();
  }
}
