import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { AuthenticationService } from '../authentication/authentication.service';
import { Vendor } from './vendor.types';

@Injectable({
  providedIn: 'root'
})
export class VendorService {
  private vendorURL = `${environment.apiBaseUrl}/vendor`;
  constructor(
    private httpClient: HttpClient,
    private authenticationService: AuthenticationService
  ) {}

  public get primarySiteId(): string {
    const userProfile = this.authenticationService.currentUserProfile$.getValue();
    if (userProfile && userProfile.primarySite) {
      return userProfile.primarySite.id;
    }
    return null;
  }

  public getVendor(vendor: string): Observable<Vendor[]> {
    return this.httpClient.get<Vendor[]>(
      `${this.vendorURL}?siteId=${this.primarySiteId}&Text=${vendor}`
    );
  }
}
