import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { SharedModule } from '../shared-module/shared-module.module';
import { AssetItemComponent } from './asset-item/asset-item.component';
import { AssetsRoutingModule } from './assets-routing.module';
import { AssetsComponent } from './assets.component';
import { ViewAssetComponent } from './view-asset/view-asset.component';
import { MatDatepickerModule } from '@angular/material/datepicker';
import {
  MatNativeDateModule,
  MAT_DATE_FORMATS,
  NativeDateAdapter,
  DateAdapter
} from '@angular/material/core';
import { formatDate } from '@angular/common';
import { dateFormatWithMonthName } from 'src/utils/dateUtils';
import { ListVendorComponent } from './view-asset/list-vendor/list-vendor.component';
import { ListGroupComponent } from './view-asset/list-group/list-group.component';
import { ListSubGroupComponent } from './view-asset/list-sub-group/list-sub-group.component';
import { ListAttachedFilesComponent } from './view-asset/list-attached-files/list-attached-files.component';

class PickDateAdapter extends NativeDateAdapter {
  format(date: Date, displayFormat: Object): string {
    if (displayFormat === 'input') {
      return formatDate(date, 'dd MMM yyyy', this.locale);
    } else {
      return date.toDateString();
    }
  }
}
@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    ReactiveFormsModule,
    AssetsRoutingModule,
    MatDatepickerModule,
    MatNativeDateModule
  ],
  declarations: [
    AssetsComponent,
    AssetItemComponent,
    ViewAssetComponent,
    ListVendorComponent,
    ListGroupComponent,
    ListSubGroupComponent,
    ListAttachedFilesComponent
  ],
  providers: [
    { provide: DateAdapter, useClass: PickDateAdapter },
    { provide: MAT_DATE_FORMATS, useValue: dateFormatWithMonthName }
  ]
})
export class AssetsModule {}
