import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { Toast, ToastService } from 'src/app/services/toast/toast.service';
import { enumValues } from 'src/utils/enumUtils';
import { formatDate } from '@angular/common';
import {
  AssetService,
  AssetType,
  Phase,
  ExtendedAsset,
  IExtendedAsset,
  IFileCollection,
  IUpdateAsset,
  AuthenticationService,
  SubGroup,
  FilehandlerService,
  Modules,
  PermissionGroup,
  AssetsPermissions
} from 'src/app/services';
import {
  fileListFileTypeValidator,
  fileListFileSizesValidator,
  fileListNumberOfFilesValidator
} from 'src/utils/formValidators';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-view-asset',
  templateUrl: './view-asset.component.html',
  styleUrls: ['./view-asset.component.scss']
})
export class ViewAssetComponent implements OnInit {
  public isLoading = false;
  public showExpandedImage = false;
  public showSaveButton = false;
  public assetId: string;
  public assetCode: string;
  public extendedAsset: ExtendedAsset;
  public form: FormGroup;
  public isEditable = false;
  public toMinDate: Date;
  public assetTypes = Object.keys(AssetType);
  public assetTypesValues: number[] = enumValues(AssetType);
  public phases = Object.keys(Phase);
  public submitAttempted = false;
  public Id: string;
  public selectedListSubGroup: SubGroup[];
  public assetSubGroupValue: string;
  public fileUploadForm: FormGroup;
  @ViewChild('fileUploadInput')
  public fileUploadInput: ElementRef;
  public fileSrcCollection: IFileCollection[] = [];
  public filesLimit = 3;
  @ViewChild('alertElementRef')
  public alertElementRef: ElementRef;
  public attachedFileCollection = new BehaviorSubject<IFileCollection[]>([]);
  public fileCount: number = 0;
  public isNewFilesAdded: boolean = false;
  public isUpdatingAsset = false;
  public savedFileSrcCollection: any;
  public filePathCollection: string[] = [];
  public fileTypes = [
    'image/jpeg',
    'image/png',
    'application/msword',
    'jpeg',
    'jpg',
    'png',
    'doc',
    'docx',
    'xls',
    'csv',
    'application/ms-excel',
    'xlsx',
    'pdf',
    'bmp'
  ];
  public isGettingUserRights: boolean;

  constructor(
    private assetService: AssetService,
    private activeRouter: ActivatedRoute,
    private formBuilder: FormBuilder,
    private authenticationService: AuthenticationService,
    private toastService: ToastService,
    private fileUploadService: FilehandlerService
  ) {
    const param = this.activeRouter.snapshot.params.id
      .toString()
      .split('&&', 2);
    this.assetId = param[0];
    this.assetCode = param[1];
    this.attachedFileCollection.next([]);
    this.checkUserEditRights();
    this.viewAssetForId();
  }

  public ngOnInit() {}

  public createForm() {
    const bulidForm = (extendedAsset: IExtendedAsset) => {
      this.form = this.formBuilder.group({
        model: [extendedAsset.model],
        manufacturer: [extendedAsset.manufacturer],
        assetGroup: [
          extendedAsset.assetGroup === null
            ? null
            : extendedAsset.assetGroup.name
        ],
        assetSubGroup: [
          extendedAsset.assetSubGroup === null
            ? null
            : extendedAsset.assetSubGroup.name
        ],
        assetGroupId: [
          extendedAsset.assetGroup === null ? null : extendedAsset.assetGroup.id
        ],
        assetSubGroupId: [
          extendedAsset.assetSubGroup === null
            ? null
            : extendedAsset.assetSubGroup.id
        ],
        assetType: [extendedAsset.assetType.id],
        kw: [extendedAsset.kw],
        volts: [extendedAsset.volts],
        phase: [extendedAsset.phase],
        loadFactor: [extendedAsset.loadFactor],
        specifications: [extendedAsset.specifications],
        vendorId: [extendedAsset.vendorId],
        frequencyDays: [
          extendedAsset.frequencyDays,
          Validators.compose([
            Validators.maxLength(4),
            Validators.pattern('^[0-9]*$')
          ])
        ],
        validFromDate: [extendedAsset.validFromDate],
        validToDate: [extendedAsset.validToDate]
      });
      this.assetSubGroupValue =
        extendedAsset.assetSubGroup === null
          ? null
          : extendedAsset.assetSubGroup.name;
      this.toMinDate = this.form.get('validFromDate').value;
    };
    bulidForm(this.extendedAsset);
    this.Id = this.extendedAsset.id;

    this.fileUploadForm = this.formBuilder.group({
      files: [
        null,
        [
          Validators.required,
          fileListFileTypeValidator(this.fileTypes),
          fileListFileSizesValidator(2000000)
        ],
        fileListNumberOfFilesValidator(this.filesLimit)
      ]
    });
  }

  public viewAssetForId(isOnInit: boolean = true) {
    const assetAttachments$: Observable<string[]> = this.assetService
      .viewAssetForId(this.assetId)
      .pipe(
        map((extendedAsset: ExtendedAsset) => {
          this.extendedAsset = extendedAsset;
          if (isOnInit) {
            this.createForm();
          }
          return extendedAsset.attachedFiles;
        })
      );
    assetAttachments$.subscribe(assetAttachments => {
      this.fileCount = assetAttachments ? assetAttachments.length : 0;
      this.fileSrcCollection = [];
      this.filePathCollection = [];
      if (assetAttachments) {
        assetAttachments.forEach(filePath => {
          const fileNameWithExt = filePath.replace(/^.*[\\\/]/, '');
          const fileName = fileNameWithExt.substring(0, fileNameWithExt.length);
          if (fileName === 'blob') {
            return;
          }
          this.filePathCollection.push(filePath);
          this.fileUploadService.getFile(filePath).subscribe(fileData => {
            fileData.name = fileName;
            this.fileSrcCollection.push(fileData);
            this.fileCount = this.fileSrcCollection
              ? this.fileSrcCollection.length
              : 0;
          });
        });
        this.fileUploadForm.setValue({
          files: this.fileSrcCollection
        });
        this.attachedFileCollection.next(this.fileSrcCollection);
      }
      this.isLoading = false;
    });
  }

  public checkUserEditRights() {
    this.isGettingUserRights = true;
    this.authenticationService
      .currentUserHasPermission({
        [PermissionGroup.Assets]: [AssetsPermissions.CanModifyAssetDetails]
      })
      .subscribe(hasPermission => {
        this.isEditable = hasPermission;
        this.isGettingUserRights = false;
      });
  }

  public updateAsset() {
    this.submitAttempted = true;
    this.isUpdatingAsset = true;
    if (
      this.form.value.validFromDate !== null &&
      this.form.value.validToDate === null
    ) {
      this.form.controls['validToDate'].setErrors({ incorrect: true });
    }
    if (this.form.valid && this.form.dirty) {
      const updateAsset: IUpdateAsset = {
        id: this.Id,
        model: this.form.value.model,
        manufacturer: this.form.value.manufacturer,
        assetGroupId: this.form.value.assetGroupId,
        assetSubGroupId: this.form.value.assetSubGroupId,
        assetTypeId: this.form.value.assetType,
        kw: this.form.value.kw,
        volts: this.form.value.volts,
        phase: this.form.value.phase,
        loadFactor: this.form.value.loadFactor,
        specifications: this.form.value.specifications,
        vendorId: this.form.value.vendorId,
        frequencyDays: this.form.value.frequencyDays,
        validFromDate: this.form.value.validFromDate
          ? formatDate(this.form.value.validFromDate, 'yyyy-MM-dd', 'en-US')
          : null,
        validToDate: this.form.value.validToDate
          ? formatDate(this.form.value.validToDate, 'yyyy-MM-dd', 'en-US')
          : null
      };
      this.assetService.updateAssetForId(updateAsset).subscribe(
        (data: ExtendedAsset) => {
          if (this.fileUploadForm.valid && this.isNewFilesAdded) {
            this.fileUploadService
              .uploadFiles(
                this.extendedAsset.assetId,
                Modules.Asset,
                this.fileSrcCollection
              )
              .subscribe(data => {
                this.savedFileSrcCollection = data;
                this.filePathCollection = data.attachments;
                this.viewAssetForId(false);
              });
          } else {
            this.viewAssetForId(false);
          }
          this.toastService.showToast(
            new Toast('Asset details saved successfully')
          );
          this.isUpdatingAsset = false;
          this.isNewFilesAdded = false;
          this.showSaveButton = false;
          this.extendedAsset = data;
          this.form.markAsPristine();
        },
        err => {
          this.toastService.showErrorToast(
            `Asset details not saved. Please try again later or contact helpdesk`
          );
          this.isUpdatingAsset = false;
          this.isNewFilesAdded = false;
          this.showSaveButton = false;
        }
      );
    }

    if (this.fileUploadForm.valid && this.isNewFilesAdded && !this.form.dirty) {
      this.fileUploadService
        .uploadFiles(
          this.extendedAsset.assetId,
          Modules.Asset,
          this.fileSrcCollection
        )
        .subscribe(data => {
          this.savedFileSrcCollection = data;
          if (this.savedFileSrcCollection.attachments) {
            this.filePathCollection = data.attachments;
            this.viewAssetForId(false);
            this.toastService.showToast(
              new Toast('Asset details saved successfully')
            );
          } else {
            this.toastService.showErrorToast(
              `Asset details not saved. Please try again later or contact helpdesk`
            );
          }
          this.isNewFilesAdded = false;
          this.showSaveButton = false;
          this.isUpdatingAsset = false;
        });
    }
    if (
      !this.fileUploadForm.valid &&
      this.isNewFilesAdded &&
      !this.form.dirty
    ) {
      this.toastService.showErrorToast(`Files not saved`);
    }
    this.isUpdatingAsset = false;
    this.showSaveButton = false;
  }

  public assignSubGroupList(group: any) {
    this.selectedListSubGroup = group.subGroup;
    this.form.get('assetGroupId').setValue(group.groupId);
    this.showSaveButton = true;
  }

  public selectedSubGroupId(subGroupId: number) {
    this.form.get('assetSubGroupId').setValue(subGroupId);
    this.showSaveButton = true;
  }

  public selectedVendorId(vendorId: string) {
    this.form.get('vendorId').setValue(vendorId);
    if (vendorId === null) {
      this.form.get('frequencyDays').setValue(null);
      this.form.get('validFromDate').setValue(null);
      this.form.get('validToDate').setValue(null);
    }
    this.showSaveButton = true;
  }

  public onDateChange(inputDate) {
    this.toMinDate = new Date(inputDate.value);
    this.toMinDate.setDate(this.toMinDate.getDate() + 1);
  }

  public handleDaysInputKeyDown(event) {
    if (event.key === 'Backspace' || event.key === 'Delete') {
      return;
    }
    if (isNaN(event.key) || event.keyCode === 32) {
      event.preventDefault();
    }
  }

  public handleInputKeyDownNumberWithPeriod(event) {
    if (
      event.key === 'Backspace' ||
      event.key === 'Delete' ||
      event.key === '.'
    ) {
      return;
    }
    if (isNaN(event.key) || event.keyCode === 32) {
      event.preventDefault();
    }
  }

  public handleDaysInput(event) {
    if (Number.isNaN(parseInt(event.target.value))) {
      this.form.controls['frequencyDays'].setValue(null);
      event.preventDefault();
      return;
    }
  }

  public handleSaveButtonView() {
    this.form.markAsPristine();
    this.showSaveButton = false;
  }

  public handleFileInputChange(): void {
    this.showSaveButton = true;
    this.isNewFilesAdded = true;
    let noExistingFile = this.fileSrcCollection.length === 0;

    const inputFiles: any[] = this.fileUploadInput.nativeElement.files;

    for (let i = 0; i < inputFiles.length; i++) {
      if (
        this.fileSrcCollection.filter(file => file.name === inputFiles[i].name)
          .length === 0
      ) {
        this.fileSrcCollection.push(inputFiles[i]);

        this.filePathCollection.push('NewFile');
      } else {
        this.toastService.showErrorToast('Cannot upload duplicate file.');
      }
    }
    this.clearFileFormErrors();

    if (noExistingFile) {
      this.fileUploadForm.setValue({
        files: this.fileSrcCollection
      });
    } else {
      this.fileUploadForm.patchValue({
        files: this.fileSrcCollection
      });
    }
    if (
      this.fileUploadForm.controls['files'].hasError('acceptableFileType') ||
      this.fileUploadForm.controls['files'].hasError(
        'acceptableNumberOfFiles'
      ) ||
      this.fileUploadForm.controls['files'].hasError('acceptableFileSize')
    ) {
      this.showSaveButton = false;
    }
    this.fileCount = this.fileSrcCollection.length;
    this.attachedFileCollection.next(this.fileSrcCollection);
  }

  public launchFileInput(): void {
    this.fileUploadForm.reset({});
    this.fileUploadInput.nativeElement.value = '';
    this.fileUploadInput.nativeElement.click();
  }

  public handleDeleteFileAttached(fileIndex: number) {
    const filePath = this.filePathCollection[fileIndex];
    if (filePath === 'NewFile') {
      this.fileSrcCollection.splice(fileIndex, 1);
      this.filePathCollection.splice(fileIndex, 1);
      this.fileCount = this.fileSrcCollection.length;
      this.attachedFileCollection.next([]);
      this.attachedFileCollection.next(this.fileSrcCollection);
      this.fileUploadForm.patchValue({
        files: this.fileSrcCollection
      });
      this.toastService.showToast(new Toast('File has been deleted.'));
      this.resetSaveButtonDisplay();
    } else {
      this.fileUploadService.deleteFile(filePath, false).subscribe(
        () => {
          this.fileSrcCollection.splice(fileIndex, 1);
          this.filePathCollection.splice(fileIndex, 1);
          this.fileCount = this.fileSrcCollection.length;
          this.attachedFileCollection.next([]);
          this.attachedFileCollection.next(this.fileSrcCollection);
          this.toastService.showToast(new Toast('File has been deleted.'));
          this.fileUploadForm.patchValue({
            files: this.fileSrcCollection
          });
          this.resetSaveButtonDisplay();
        },
        (err: HttpErrorResponse) => {
          this.toastService.showErrorToast('Unable to delete the file.');
          return;
        }
      );
    }

    this.fileUploadForm.markAsPristine();
    this.fileUploadForm.markAsUntouched();
    this.fileUploadForm.updateValueAndValidity();
  }

  public resetSaveButtonDisplay() {
    if (this.fileSrcCollection.length === 0) {
      this.showSaveButton = false;
    }
    if (this.fileSrcCollection.length === 3) {
      this.showSaveButton = true;
    }
  }

  public clearFileFormErrors() {
    this.fileUploadForm.controls['files'].setErrors({
      acceptableFileType: false,
      acceptableFileSize: false
    });
  }
}
