import {
  Component,
  OnDestroy,
  OnInit,
  ViewChildren,
  ElementRef,
  ViewChild,
  QueryList,
  Input,
  Output,
  EventEmitter
} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Subscription } from 'rxjs';
import { debounceTime, filter } from 'rxjs/operators';
import { VendorService } from 'src/app/services/vendor/vendor.service';
import { Vendor } from 'src/app/services/vendor/vendor.types';

@Component({
  selector: 'app-list-vendor',
  templateUrl: './list-vendor.component.html',
  styleUrls: ['./list-vendor.component.scss']
})
export class ListVendorComponent implements OnInit, OnDestroy {
  public listVendorResultsVisible = false;
  public isVendorSearching = false;
  public listVendorResults: Vendor[] = [];
  public listVendorForm: FormGroup;
  public listVendorSubscription: Subscription;
  @ViewChildren('searchResult')
  public searchResultElements: QueryList<ElementRef>;
  @ViewChild('searchInput')
  public searchInputElement: ElementRef;
  public isVendorSelected = false;
  @Input() isEditable: boolean;
  @Input() vendorName: string;
  @Output() selectedVendorId = new EventEmitter<string>();

  constructor(
    private formBuilder: FormBuilder,
    private vendorService: VendorService
  ) {
    this.listVendorForm = this.formBuilder.group({
      text: '',
      vendorId: null
    });
  }

  public ngOnInit(): void {
    this.listVendorForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),

        filter(textValue => textValue.length >= 3),
        filter(textValue => !this.vendorNameIsInListResults(textValue))
      )
      .subscribe(() => this.listVendor());

    this.listVendorForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length < 3)
      )
      .subscribe(() => {
        this.listVendorResultsVisible = false;
      });

    this.listVendorForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length === 0)
      )
      .subscribe(() => {
        this.selectedVendorId.emit(null);
      });

    if (this.vendorName) {
      this.listVendorForm.setValue({ text: this.vendorName, vendorId: '' });
    }
  }

  public listVendor() {
    this.listVendorSubscription &&
      !this.listVendorSubscription.closed &&
      this.listVendorSubscription.unsubscribe();
    const textValue = this.listVendorForm.get('text').value;
    if (this.isVendorSelected === false) {
      this.isVendorSearching = true;
      this.listVendorResultsVisible = this.listVendorForm.get('text').dirty
        ? true
        : false;
      this.listVendorSubscription = this.vendorService
        .getVendor(textValue)
        .subscribe((data: Vendor[]) => {
          this.listVendorResults = data;
          this.isVendorSearching = false;
        });
    }
  }

  public handleVendorListKeyUp(event) {
    this.isVendorSelected = false;
    switch (event.key) {
      case 'ArrowDown':
        const resultButtons = this.searchResultElements.toArray();
        resultButtons.length > 0 && resultButtons[0].nativeElement.focus();
        break;
      case 'Escape':
        this.clearlistVendorForm();
        break;
    }
  }

  public handleVendorListResultKeyUp(event) {
    switch (event.key) {
      case 'ArrowUp':
        const firstResultButton = this.searchResultElements.toArray()[0]
          .nativeElement;
        if (event.target === firstResultButton) {
          this.searchInputElement.nativeElement.focus();
        } else {
          event.target.previousSibling.focus();
        }
        break;
      case 'ArrowDown':
        if (event.target.nextElementSibling != null) {
          event.target.nextSibling && event.target.nextSibling.focus();
        }
        break;
      case 'Escape':
        this.clearlistVendorForm();
        break;
    }
  }

  public handleVendorListResultClick(vendor: Vendor) {
    this.listVendorForm.setValue({
      text: vendor.vendorInfo,
      vendorId: vendor.id
    });
    this.listVendorResultsVisible = false;
    this.isVendorSelected = true;
    this.selectedVendorId.emit(vendor.vendorInfo);
  }

  public handleVendorListBlur() {
    setTimeout(() => {
      const searchValue = this.listVendorForm.get('text').value;
      const activeElement: HTMLElement = document.activeElement as HTMLElement;
      if (
        this.vendorNameIsInListResults(searchValue) ||
        activeElement.dataset.sitename
      ) {
        return;
      }
      if (!searchValue) {
        this.clearlistVendorForm();
      }
    }, 0);
  }

  public vendorNameIsInListResults(searchText: string) {
    let result = false;
    for (const vendor of this.listVendorResults) {
      if (vendor.vendorInfo === searchText) {
        result = true;
      }
    }
    return result;
  }

  public clearlistVendorForm() {
    this.listVendorForm.setValue({ text: '', vendorId: null });
    this.listVendorResultsVisible = false;
  }

  public ngOnDestroy() {
    this.listVendorSubscription &&
      !this.listVendorSubscription.closed &&
      this.listVendorSubscription.unsubscribe();
  }
}
