import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Asset } from 'src/app/services/asset/asset.types';

@Component({
  selector: 'app-asset-item',
  templateUrl: './asset-item.component.html',
  styleUrls: ['./asset-item.component.scss']
})
export class AssetItemComponent implements OnInit {
  @Input()
  public assetItem: Asset;

  constructor(private router: Router) {}

  ngOnInit(): void {}

  public handleSelectedAssetClick(assetId: string, assetCode: string) {
    this.router.navigate(['assets', assetId + '&&' + assetCode]);
  }
}
