import {
  Component,
  OnDestroy,
  OnInit,
  ViewChildren,
  ElementRef,
  ViewChild,
  QueryList,
  Input,
  Output,
  EventEmitter
} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Subscription } from 'rxjs';
import { debounceTime, filter } from 'rxjs/operators';
import {
  Group,
  AssetGroupAndSubGroup,
  SubGroup
} from '../../../services/asset/asset.types';
import { SubGroupWizard } from '../list-subgroup.wizard';
import { AssetService } from 'src/app/services/asset/asset.service';

@Component({
  selector: 'app-list-group',
  templateUrl: './list-group.component.html',
  styleUrls: ['./list-group.component.scss']
})
export class ListGroupComponent implements OnInit, OnDestroy {
  public listResultsVisible = false;
  public isSearching = false;
  public searchResults: AssetGroupAndSubGroup[] = [];
  public listGroupForm: FormGroup;
  public listGroupsSubscription: Subscription;
  @ViewChildren('searchResult')
  public searchResultElements: QueryList<ElementRef>;
  @ViewChild('searchInput')
  public searchInputElement: ElementRef;
  public isGroupSelected = false;
  @Input() isEditable: boolean;
  @Input() assetGroupName: string;
  @Input() assetGroupId: number;
  @Output() listSubgroups = new EventEmitter<{
    subGroup: SubGroup[];
    groupId: number;
  }>();

  constructor(
    private formBuilder: FormBuilder,
    private assetService: AssetService,
    public subGroupWizard: SubGroupWizard
  ) {
    this.listGroupForm = this.formBuilder.group({
      text: '',
      groupId: null
    });
  }

  public ngOnInit(): void {
    this.listGroupForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length >= 3),
        filter(textValue => !this.groupNameIsInListResults(textValue))
      )
      .subscribe(() => this.listGroup());

    this.listGroupForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length < 3)
      )
      .subscribe(() => {
        this.listResultsVisible = false;
      });
    if (this.assetGroupName) {
      this.listGroupForm.setValue({ text: this.assetGroupName, groupId: '' });
    }
  }

  public listGroup() {
    this.listGroupsSubscription &&
      !this.listGroupsSubscription.closed &&
      this.listGroupsSubscription.unsubscribe();
    const textValue = this.listGroupForm.get('text').value;
    if (this.isGroupSelected === false) {
      this.isSearching = true;
      this.listResultsVisible = this.listGroupForm.get('text').dirty
        ? true
        : false;
      this.listGroupsSubscription = this.assetService
        .getAssetGroup(textValue)
        .subscribe((data: AssetGroupAndSubGroup[]) => {
          this.searchResults = data;
          this.isSearching = false;
        });
    }
  }

  public handleListKeyUp(event) {
    this.isGroupSelected = false;
    switch (event.key) {
      case 'ArrowDown':
        const resultButtons = this.searchResultElements.toArray();
        resultButtons.length > 0 && resultButtons[0].nativeElement.focus();
        break;
      case 'Escape':
        this.clearlistGroupForm();
        break;
    }
  }

  public handleListResultKeyUp(event) {
    switch (event.key) {
      case 'ArrowUp':
        const firstResultButton = this.searchResultElements.toArray()[0]
          .nativeElement;
        if (event.target === firstResultButton) {
          this.searchInputElement.nativeElement.focus();
        } else {
          event.target.previousSibling.focus();
        }
        break;
      case 'ArrowDown':
        if (event.target.nextElementSibling != null) {
          event.target.nextSibling && event.target.nextSibling.focus();
        }
        break;
      case 'Escape':
        this.clearlistGroupForm();
        break;
    }
  }

  public handleListResultClick(group: Group) {
    let selectedGroup: AssetGroupAndSubGroup[];
    this.listGroupForm.setValue({
      text: group.name,
      groupId: group.id
    });
    this.listResultsVisible = false;
    this.isGroupSelected = true;
    selectedGroup = this.searchResults.filter(
      group => group.name === this.listGroupForm.get('text').value
    );
    this.subGroupWizard.reset();
    this.subGroupWizard.listSubGroup = selectedGroup[0].assetSubGroups;
    this.listSubgroups.emit({
      subGroup: this.subGroupWizard.listSubGroup,
      groupId: group.id
    });
  }

  public handleListBlur() {
    setTimeout(() => {
      const searchValue = this.listGroupForm.get('text').value;
      const activeElement: HTMLElement = document.activeElement as HTMLElement;
      if (
        this.groupNameIsInListResults(searchValue) ||
        activeElement.dataset.sitename
      ) {
        return;
      }
      this.clearlistGroupForm();
    }, 0);
  }

  public groupNameIsInListResults(searchText: string) {
    let result = false;
    for (const group of this.searchResults) {
      if (group.name === searchText) {
        result = true;
      }
    }
    return result;
  }

  public clearlistGroupForm() {
    this.listGroupForm.setValue({ text: '', groupId: null });
    this.listResultsVisible = false;
  }

  public ngOnDestroy() {
    this.listGroupsSubscription &&
      !this.listGroupsSubscription.closed &&
      this.listGroupsSubscription.unsubscribe();
  }
}
