import {
  Component,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  EventEmitter
} from '@angular/core';
import { Observable } from 'rxjs';
import { IFileCollection } from 'src/app/services';

@Component({
  selector: 'app-list-attached-files',
  templateUrl: './list-attached-files.component.html',
  styleUrls: ['./list-attached-files.component.scss']
})
export class ListAttachedFilesComponent implements OnInit, OnChanges {
  @Input() public attachedFileCollection: Observable<IFileCollection[]>;
  @Input() public isEditable: boolean;
  public fileCollection: IFileCollection[];
  @Output() public deletedFileIndex = new EventEmitter<number>();

  constructor() {}

  public ngOnInit() {}

  public handleDeleteItem(fileIndex: number) {
    this.deletedFileIndex.emit(fileIndex);
  }

  public handleOpenFile(fileToOpen: any) {
    let fileURL;
    if (fileToOpen.file) {
      fileURL = URL.createObjectURL(fileToOpen.file);
    } else {
      fileURL = URL.createObjectURL(fileToOpen);
    }
    window.open(fileURL, '_blank');
  }

  public ngOnChanges(changes: SimpleChanges): void {
    if (changes['attachedFileCollection'] && this.attachedFileCollection) {
      this.attachedFileCollection.subscribe(value => {
        setTimeout(() => {
          this.fileCollection = value;
        });
      });
    }
  }
}
