import {
  Component,
  OnInit,
  ViewChildren,
  ElementRef,
  ViewChild,
  QueryList,
  Input,
  Output,
  EventEmitter,
  OnChanges
} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { debounceTime, filter } from 'rxjs/operators';
import { SubGroup } from '../../../services/asset/asset.types';

@Component({
  selector: 'app-list-sub-group',
  templateUrl: './list-sub-group.component.html',
  styleUrls: ['./list-sub-group.component.scss']
})
export class ListSubGroupComponent implements OnInit, OnChanges {
  public listSubGroupResultsVisible = false;
  public isSubGroupSearching = false;
  public listSubGroupResults: SubGroup[] = [];
  public listSubGroupForm: FormGroup;
  @ViewChildren('searchResult')
  public searchResultElements: QueryList<ElementRef>;
  @ViewChild('searchInput')
  public searchInputElement: ElementRef;
  public isSubGroupSelected = false;
  @Input() listSubGroups: SubGroup[];
  @Input() assetSubGroupName: string;
  @Output() subGroupId = new EventEmitter<number>();

  constructor(private formBuilder: FormBuilder) {
    this.listSubGroupForm = this.formBuilder.group({
      text: '',
      subGroupId: null
    });
  }

  public ngOnInit(): void {
    this.listSubGroupForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length >= 3),
        filter(textValue => !this.subGroupNameIsInListResults(textValue))
      )
      .subscribe(() => this.listSubGroup());

    this.listSubGroupForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length < 3)
      )
      .subscribe(() => {
        this.listSubGroupResultsVisible = false;
      });
    if (this.assetSubGroupName) {
      this.listSubGroupForm.get('text').setValue(this.assetSubGroupName);
    }
  }

  public listSubGroup() {
    if (this.listSubGroups && this.isSubGroupSelected === false) {
      const textValue = this.listSubGroupForm.get('text').value.toLowerCase();
      this.isSubGroupSearching = true;
      this.listSubGroupResultsVisible = this.listSubGroupForm.get('text').dirty
        ? true
        : false;
      this.listSubGroupResults = this.listSubGroups.filter(subGroup =>
        subGroup.name.toLowerCase().includes(textValue)
      );
      this.isSubGroupSearching = false;
    }
  }

  public handleSubGroupListKeyUp(event) {
    this.isSubGroupSelected = false;
    switch (event.key) {
      case 'ArrowDown':
        const resultButtons = this.searchResultElements.toArray();
        resultButtons.length > 0 && resultButtons[0].nativeElement.focus();
        break;
      case 'Escape':
        this.clearlistSubGroupForm();
        break;
    }
  }

  public handleSubGroupListResultKeyUp(event) {
    switch (event.key) {
      case 'ArrowUp':
        const firstResultButton = this.searchResultElements.toArray()[0]
          .nativeElement;
        if (event.target === firstResultButton) {
          this.searchInputElement.nativeElement.focus();
        } else {
          event.target.previousSibling.focus();
        }
        break;
      case 'ArrowDown':
        if (event.target.nextElementSibling != null) {
          event.target.nextSibling && event.target.nextSibling.focus();
        }
        break;
      case 'Escape':
        this.clearlistSubGroupForm();
        break;
    }
  }

  public handleSubGroupListResultClick(subGroup: SubGroup) {
    this.listSubGroupForm.setValue({
      text: subGroup.name,
      subGroupId: subGroup.id
    });
    this.listSubGroupResultsVisible = false;
    this.isSubGroupSelected = true;
    this.subGroupId.emit(subGroup.id);
  }

  public handleSubGroupListBlur() {
    setTimeout(() => {
      const searchValue = this.listSubGroupForm.get('text').value;
      if (this.subGroupNameIsInListResults(searchValue) || searchValue !== '') {
        return;
      }
      this.clearlistSubGroupForm();
    }, 0);
  }

  public subGroupNameIsInListResults(searchText: string) {
    let result = false;
    for (const subGroup of this.listSubGroupResults) {
      if (subGroup.name === searchText) {
        result = true;
      }
    }
    return result;
  }

  public clearlistSubGroupForm() {
    this.listSubGroupForm.setValue({ text: '', subGroupId: null });
    this.listSubGroupResultsVisible = false;
  }

  public ngOnChanges(): void {
    this.clearlistSubGroupForm();
  }
}
